

.header_home {
    background-color: #2F303A;
    background-image: url(/src/images/abstract-background-mob.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    // margin: 0px auto;
    height: 230px;

}

@media screen and (min-width: 768px) {
    .header_home {
        background-image: url(/src/images/abstract-background-tab.jpg);
        height: 216px;
    }
}

@media screen and (min-width: 1280px) {
    .header_home {
        background-image: url(/src/images/abstract-background.jpg);
        height: 216px;

    }
}

.film {
    width: 24px;
    height: 24px;

}

.navigation-main-list {
    display: flex;
    justify-content: space-between;
    width: 100%;

}

.navigation-list-ferst {
    display: flex;
    gap: 8px;
    padding-top: 40px;

}

.navigation-list-second {
    display: flex;
    gap: 40px;
    padding-top: 49px;

}

.nav-text {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: calc(14px /12px);
    align-items: center;
    text-align: center;
    text-transform: uppercase;

    color: #FFFFFF;
}

.nav-logo {
    font-weight: 500;
    font-size: 30px;

    line-height: calc(35px /30px);
    display: none;
    align-items: center;
    text-align: center;

    color: #FFFFFF;
}

@media screen and (min-width: 768px) {
    .nav-logo {
        display: flex;
    }
}

.aktive {
    position: relative;
}

.aktive::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 3px;
    background: #FF001B;
    // border-radius: 5px;
    left: 0;
    bottom: -3px;
}

.search-form {
    margin-top: 53px;
    text-align: center;
}

.search-input {
    background-color: transparent;
    width: 280px;
    height: 20px;
    border-color: transparent;
    font-size: 14px;
    border-bottom-color: #FFFFFF;
    line-height: calc(16px /14px);
    color: #FFFFFF;

    &::placeholder {

        font-size: 14px;
        line-height: calc(16px /14px);
        text-justify: auto;


        color: #FFFFFF;


    }

}

@media screen and (min-width: 768px) {
    .search-input {
        width: 336px;
    }
}

@media screen and (min-width: 768px) {
    .search-input {
        width: 394px;
    }
}

.error {
margin-top: 20px;
    font-size: 14px;
    line-height: calc(16px /14px);
    align-items: center;
    text-align: center;
    color: #FF001B;
}